@import url(https://fonts.googleapis.com/css?family=Josefin+Sans);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}



body{ 
  background-image: url(/greenhousecoder/koodi2.git/static/media/bg.db29b234.png);
  
}

.App {
  margin-left: 0vh;
  margin-top: -5vh;
  margin-bottom: 0vh;
  background-image: url(/greenhousecoder/koodi2.git/static/media/bg.db29b234.png);
  min-width: 100vw;
  opacity: 0.1;
}
h1{
  margin-left: -4vw;
  color: red;
  text-align:center;
  margin-top: -2vh;
  text-shadow: -3px 0px  orange, 1px 0 #ddd, 0 3px lightblue;
  font-size: 7vh;
  font-family: 'Orbitron', sans-serif;
  -webkit-transform: rotate(-2deg);
          transform: rotate(-2deg);
  padding: 5vh;
}

h4{
  text-align: center;
  color: rgba(00,0,0,0.8);
  margin-top: -10vh;
  font-family: 'Orbitron', sans-serif;
  text-shadow: 1px 1px  1px rgba(200,200,200,0.9);
  margin-bottom: 5vh;
}
#spanni{
  font-size: 3.5rem;
  margin-bottom: -5rem;
  color: orange;
}

.wrap{
  min-width: 50vw;
  max-width: 50vw;
  max-height: 50vh;
  padding: 2vw;
  margin: 0 auto;
  font-size: 2vh;
  font-family: monospace, 'Orbitron', sans-serif;    
}




@media only screen and (max-width: 600px) {

  h4{
}
h1{
  margin-left: 0vw;
}
.wrap{
  max-width: 50vw;
}

}

.box{
    overflow:auto ;
    overflow-x: hidden;
    scrollbar-color: light;
    min-height: 40vh;
    max-height: 40vh;

}
.rivitbox{
    font-size: 2.5vh;
    padding: 2vh;
    width: 50vw;
    padding-left: 0vw;
    
    color: #777;
text-align: center;
border-bottom: 1px solid;

}
.tyyppi{
    float: left;
    display: flex;
    justify-content: space-between;
    color: #777;
    margin-right: 0vw;
    min-width: 15vw;
    max-width: 15vw;
    margin-left: 2vw;
    overflow: hidden;
    padding: 0.5vw;
    

}
.tehot{
padding: 1.1vh;
}
.tyyppi_info{
    float: left;
    padding: 0.5vw;
    margin: 0 0 0 5vh;   
text-align: center;
}
